import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ChapterService } from './../../../../home/services/chapter.service';
import { Chapter } from './../../../../home/interfaces/Chapter';
import { ArticleService } from './../../../../home/services/article.service';
import { Article } from './../../../../home/interfaces/Article';
import { ActivatedRoute} from '@angular/router';

declare var $: any;

@Component({
  selector: 'app-chapter-heart-image-title',
  templateUrl: './chapter-heart-image-title.component.html',
  styleUrls: ['./chapter-heart-image-title.component.css']
})
export class ChapterHeartImageTitleComponent implements OnInit, AfterViewInit {
  //slugChapter : string;
  chapter : Chapter;
  article : Article[] = [];
  constructor(
    private _serviceChapter : ChapterService,
    private _activatedRoute : ActivatedRoute,
    private _serviceAriticle : ArticleService,
  ) {
      
   }
  ngAfterViewInit() {
    setTimeout(()=>{
      this.doJquery();
    },1000)
  }
  ngOnInit() {
    this.article = [];
    this._activatedRoute.params.subscribe(
      data => {
        this.getChapter(data.name);
        jQuery('#show-menu-in-chapter > ul > li > ul').removeClass('add-display-flex');
      }
    );
  }

  doJquery() {
    $(function() {
      jQuery(document).ready(function () {
        jQuery('#show-menu-in-chapter > ul > li > div > i').click(function () {
          jQuery(this).closest('li').find('ul').toggleClass('add-display-flex');
          jQuery("#show-menu-in-chapter > ul > li > div > i").toggleClass("fa-ellipsis-v fix-left-i");
          jQuery('section#image-title').css('height', '100%');
          jQuery('section#image-title').css('background-position', 'center top');
          jQuery('#show-menu-in-chapter > ul > li > ul > a').click(function () {
            jQuery('#show-menu-in-chapter > ul > li > div > i').closest('li').find('ul').removeClass('add-display-flex');
            jQuery('section#image-title').css('height', '100%');
            jQuery('section#image-title').css('background-position', 'center top');
            jQuery('#show-menu-in-chapter').css('padding-bottom', '0px');
          });
        });       
      });
    });
    
  }

  getAllArticle(id : number){
    this.article = [];
    this._serviceAriticle.getArticle().subscribe(
      data =>  {
        data.results.forEach(element => {
          if(element.chapter_id==id){
            this.article.push(element);
          }
        });
      }
    )
  }

  getChapter(name : string){
    this._serviceChapter.getChapterByName(name).subscribe(
      data => {
        this.chapter = data;
        this.getAllArticle(data.id);
      }
    )
  }

}
