import { LOCAL_STORAGE , WINDOW} from '@ng-toolkit/universal';
import { Component, OnInit , Inject} from '@angular/core';
import { StoriesService } from './../../../stories/services/stories.service';
import { IStories } from './../../../stories/interfaces/IStories';
import { ChapterService } from './../../../home/services/chapter.service';
import { Chapter } from './../../../home/interfaces/Chapter';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { NewLetterService } from './../../../footer/services/new-letter.service';
import swal from 'sweetalert2';

@Component({
  selector: 'app-home-inspiring-stories',
  templateUrl: './home-inspiring-stories.component.html',
  styleUrls: ['./home-inspiring-stories.component.css']
})
export class HomeInspiringStoriesComponent implements OnInit {

  stories : IStories[] = [];
  chapter : Chapter;
  count : number = 0;
  limit : number = 8;

  frmPopupNews: FormGroup;
  isDisplay: boolean = false;

  key: string;

  fristChapter: string;
  fristArticle: string;

  
  constructor(@Inject(WINDOW) private window: Window, @Inject(LOCAL_STORAGE) private localStorage: any, 
    private _serviceStories : StoriesService,
    private _serviceChapter: ChapterService,
    private _router : Router,
    private _serviceNewsLetter: NewLetterService,
    private _formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.createForm();
    this.getAllStories();
    this._serviceChapter.getChapter().subscribe(
      data => {
        this.chapter = data.results;
        this.fristChapter = data.results[0].slug;
        this.fristArticle = data.results[0].articles[0].slug;
      }
    )
  }

  getAllStories(){
    this._serviceStories.getStories().subscribe(
      data => {
          data.results.forEach((element,index)=>{
          if(index < this.limit){
            this.stories.push(element)
            this.count++;
          }
        })
      }
    )
  }

  onClickStoriesId(id : string){
    this._router.navigate( [ '/entrepreneurs-stories' ], { fragment: id } )
  }

  onStartReading(){
    if (this.localStorage.getItem(this.key) != null) {
      // We have items key = email
     
      this._router.navigate( [ `/${this.fristChapter}/${this.fristArticle}` ], { fragment: 'content-in-chapter' } );

    } else {
      // No items
      var modal = document.getElementById('popup-newsletters');
      var button = document.getElementById('close-btn-download-newss');
      modal.style.display = "block";
      button.onclick = function () {
        modal.style.display = "none";
      }
      // When the user clicks anywhere outside of the modal, close it
      this.window.onclick = function (event) {
        if (event.target == modal) {
          modal.style.display = "none";
        }
      }
    }
  }


  createForm() {
    this.frmPopupNews = this._formBuilder.group({
      name: ['', [
        Validators.required,
        Validators.minLength(1),
        Validators.maxLength(50)
      ]],
      email: ['', [
        Validators.required,
        Validators.maxLength(50),
        Validators.pattern('[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}')
      ]]
    });
  }


  onSubmitFormNewss() {
    if (this.frmPopupNews.valid) {
      //console.log(this.frmPopupNews.value);
      this.localStorage.setItem(this.key, this.frmPopupNews.value.email);
      this._serviceNewsLetter.sendNewLetter(this.frmPopupNews.value).subscribe(data => data);
      this.frmPopupNews.reset();
      swal({
        title: 'Subscribed',
        type: 'success',
        confirmButtonText: 'OK'
      }).then((results) => {
        if (results.value) {
          
          this._serviceChapter.getChapter().subscribe(
            data => {
              this._router.navigate( [ '/',data.results[0].slug, data.results[0].articles[0].slug ], { fragment: 'content-in-chapter' } );

            }
          )
        }
      })
      this.isDisplay = !this.isDisplay;
    } else {
      this.validateAllFormFields(this.frmPopupNews);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {         //{1}
    Object.keys(formGroup.controls).forEach(field => {    //{2}
      const control = formGroup.get(field);               //{3}
      if (control instanceof FormControl) {             //{4}
        control.markAsDirty({ onlySelf: true });
      } else if (control instanceof FormGroup) {        //{5}
        this.validateAllFormFields(control);            //{6}
      }
    });
  }
}
