import { WINDOW } from '@ng-toolkit/universal';
import { Component, OnInit, Inject} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ChapterService } from './../../../home/services/chapter.service';
import { Chapter } from './../../../home/interfaces/Chapter';
@Component({
  selector: 'app-chapter-heart',
  templateUrl: './chapter-heart.component.html',
  styleUrls: ['./chapter-heart.component.css']
})
export class ChapterHeartComponent implements OnInit {

  // slugChapter: string;
  checkChapter : boolean;
  constructor(@Inject(WINDOW) private window: Window, 
    private activateRoute : ActivatedRoute,
    private serviceChapter : ChapterService,
    private router : Router
  ) {
    
  }

  ngOnInit() {
    this.activateRoute.params.subscribe(
      data => {
        this.serviceChapter.getChapter().subscribe(
          chapter => {
            chapter.results.forEach(element => {
              if(element.chapter_name=data.name){
                this.checkChapter = true;
              }else{
                this.checkChapter = false;
              }
            });
          }
        );
      }
    );
    if(this.checkChapter==false){
      this.router.navigate(['/page-not-found']);
    }
  }

  ngAfterViewInit() {
    this.window.scrollTo(0, 0);
  }

}
