import { WINDOW } from '@ng-toolkit/universal';
import { Component, OnInit, AfterViewInit, AfterViewChecked , Inject} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
// import { ArticleService } from './../../../../../../home/services/article.service';
import { Article } from './../../../../../../home/interfaces/Article';
import { ChapterService } from './../../../../../../home/services/chapter.service';
import { Chapter } from './../../../../../../home/interfaces/Chapter';
import { IStories } from './../../../../../../stories/interfaces/IStories';
import { ViewEncapsulation } from '@angular/core';
import { Paragraph } from './../../../../../../home/interfaces/Paragraph';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-chapter-heart-content-grid-article-section1',
  templateUrl: './chapter-heart-content-grid-article-section1.component.html',
  styleUrls: ['./chapter-heart-content-grid-article-section1.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class ChapterHeartContentGridArticleSection1Component implements OnInit, AfterViewInit, AfterViewChecked {

  stories: IStories[] = [];
  tipAndTrick: string;
  ask_yourseft: string;
  colorRgbaChapter: string;
  checkLink: boolean = false;
  article: Article;

  paragraph: Paragraph[];

  listChapter: Chapter[] = [];
  slugChapter: string[];
  chapterID: number;

  chapter: Chapter;
  chapterNext: Chapter;
  listArticleByIDChapter: Article[] = [];

  index: number;
  displayNext: boolean = false;
  displayPrevious: boolean = false;
  displayNextChapter: boolean = false;

  slugNameLastChapter: string;

  titleNext: string;
  titlePreious: string;

  private scrollExecuted: boolean = false;

  constructor(@Inject(WINDOW) private window: Window, 
    private _activatedRoute: ActivatedRoute,
    private _serviceChapter: ChapterService,
    private router: Router
  ) { }

  ngOnInit() {
    this.getAllChapter();
    this.getParentUrl();

  }

  ngAfterViewInit() {
    this.window.scrollTo(0, 0);
  }

  ngAfterViewChecked() {
    //setTimeout(()=>{
    if (!this.scrollExecuted) {
      let routeFragmentSubscription: Subscription;

      // Automatic scroll
      routeFragmentSubscription = this._activatedRoute.fragment.subscribe(
        fragment => {
          if (fragment) {
            let element = document.getElementById(fragment);
            //console.log(element);
            if (element) {
              //element.scrollIntoView();
              let length = $("#image-title").height();
              //console.log(length);
              $("html, body").animate({ scrollTop: $([element]).offset().top - (length-300) }, 1000);
              this.scrollExecuted = true;

              // Free resources
              setTimeout(
                () => {
                  //console.log('routeFragmentSubscription unsubscribe');
                  routeFragmentSubscription.unsubscribe();
                }, 1000);
            }
          }
        });
    }
    //},3000)
  }

  getParentUrl() {
    this.slugChapter = this._activatedRoute.parent.snapshot.pathFromRoot
      .map(s => s.url).reduce((a, e) => a.concat(e)).map(s => s.path);
    this.getAriticleByChapter(this.slugChapter[0]);
  }

  getAriticleByChapter(slugChapter: string) {
    this._activatedRoute.params.subscribe(
      dataname => {
        this._serviceChapter.getChapterByName(slugChapter).subscribe(
          data => {
            this.listArticleByIDChapter = data.articles;
            this.chapter = data;
            this.convertHex(data.color, 10);
            data.articles.forEach(
              (element, index) => {
                if (element.slug == dataname.name) {
                  this.checkLink = true;
                  this.article = element;

                  this.get4CustomContent(element.paragraphs);

                  this.index = index;

                  if (index == 0 && index == (data.articles.length - 1)) {
                    this.displayNext = false;
                    this.displayPrevious = false;
                    this.displayNextChapter = true;
                  }
                  else if (index == 0) {
                    this.displayNext = true;
                    this.displayPrevious = false;
                    this.displayNextChapter = false;
                    this.titleNext = data.articles[index + 1].title;
                  }
                  else if (index == (data.articles.length - 1)) {
                    this.displayNext = false;
                    this.displayPrevious = true;
                    this.displayNextChapter = true;
                    this.titlePreious = data.articles[index - 1].title;
                    this._serviceChapter.getChapter().subscribe(last => {
                      last.results.forEach(
                        (element, index) => {
                          if (index == (last.results.length - 1)) {
                            if (this.slugChapter[0] == element.slug) {
                              this.displayNextChapter = false;
                            }
                          }
                        }
                      );
                    });

                  }
                  else {
                    this.displayNext = true
                    this.displayPrevious = true;
                    this.displayNextChapter = false;
                    this.titleNext = data.articles[index + 1].title;
                    this.titlePreious = data.articles[index - 1].title;
                  }
                }
              }
            );

            if (this.checkLink == false) {
              this.router.navigate(['/page-not-found']);
            }
          }
        )
      }
    );
  }

  getAllChapter() {
    this._serviceChapter.getChapter().subscribe(
      data => {
        this.listChapter = data.results;
        this.getChapterNext();
      }
    )
  }

  clickEventNext(indexArticle: number) {

    this.listArticleByIDChapter.forEach(
      (element, index) => {
        if (index == (indexArticle + 1)) {
          this.article = element;
          this.index = index;
          this.router.navigate([`/${this.slugChapter}/${element.slug}`]);
          $('html, body').animate({
            scrollTop: $("#content-in-chapter").offset().top - 100
          }, 1000);
        }
      }
    );
  }

  clickEventPrevious(indexArticle: number) {

    this.listArticleByIDChapter.forEach(
      (element, index) => {
        if (index == (indexArticle - 1)) {
          this.article = element;
          this.index = index;
          this.router.navigate([`/${this.slugChapter}/${element.slug}`]);
          $('html, body').animate({
            scrollTop: $("#content-in-chapter").offset().top - 100
          }, 1000);
        }
      }
    );
  }

  getChapterNext() {
    let indextemp: number;
    this.listChapter.forEach(
      (element, index) => {
        if (element.slug == this.slugChapter[0]) {
          indextemp = index;
        };
        if (index == (indextemp + 1)) {
          this.chapterNext = element;
        }
      }
    )
  }

  clickEventNextchapter() {
    let indextemp: number;
    this.listChapter.forEach(
      (element, index) => {
        if (element.slug == this.slugChapter[0]) {
          indextemp = index;
        };
        if (index == (indextemp + 1)) {
          this.chapterNext = element;
          this.router.navigate([`/${this.chapterNext.slug}`]);
          this.window.scrollTo(0, 0);
        }
      }
    )
  }

  getSlugLastChapter(chapterList: Chapter[]) {
    chapterList.forEach(
      (element, index) => {
        if (index == (chapterList.length - 1)) {
          this.slugNameLastChapter = element.slug;
        }
      }
    );
  }

  get4CustomContent(paragraph: Paragraph[]) {
    this.stories = [];
    //console.log(paragraph);
    this.paragraph = paragraph;
  }

  convertHex(hex, opacity) {
    hex = hex.replace('#', '');
    let r = parseInt(hex.substring(0, 2), 16);
    let g = parseInt(hex.substring(2, 4), 16);
    let b = parseInt(hex.substring(4, 6), 16);

    this.colorRgbaChapter = 'rgba(' + r + ',' + g + ',' + b + ',' + opacity / 100 + ')';
  }
}
