  ngOnInit() {
    this.getAllChapter();
    this.createForm();
    this.createFormPopup();
    this._serviceSendMail.listLanguage().subscribe(
      data => {
        // console.log(data);
        this.listLangage = data;
      }
    );
    this.window.scrollTo(0, 0);
    this.hoverLanguage();
  }

  getAllChapter(){
    this._serviceChapter.getChapter().subscribe(
      data =>  
      {
        this.chapter = data.results;
        this.fristChapter = data.results[0].slug;
        this.fristArticle = data.results[0].articles[0].slug;
      }
    )
  }

  createForm(){
    this.frmSendEmail = this._formBuilder.group({
      email : ['',[
        Validators.required,
        Validators.maxLength(50),
        Validators.pattern('[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}')
      ]],
    });
  }

  onSubmitFormSendLetter(){
    if(this.frmSendEmail.valid){
      console.log(this.frmSendEmail.value);
      this._serviceNewLetter.sendNewLetter(this.frmSendEmail.value).subscribe(data=>console.log(data));
      this.frmSendEmail.reset();
        swal({
          title : 'Subscribed',
          type : 'success',
          confirmButtonText: 'OK'
        })
    }else{
      this.validateAllFormFields(this.frmSendEmail);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {         //{1}
  Object.keys(formGroup.controls).forEach(field => {    //{2}
    const control = formGroup.get(field);               //{3}
      if (control instanceof FormControl) {             //{4}
        control.markAsDirty({ onlySelf: true });
      } else if (control instanceof FormGroup) {        //{5}
        this.validateAllFormFields(control);            //{6}
      }
    });
  }

  getValueLanguage(valueLang : string){
    this.selectedValue = valueLang;

  }

