import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { HttpClient, HttpErrorResponse, HttpHeaders  } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { Article } from './../interfaces/Article';
import { Router } from '../../../../../node_modules/@angular/router';

@Injectable({
  providedIn: 'root'
})
export class ArticleService {

  url : string = environment.apiUrl;
  headers: HttpHeaders;
  constructor(
    private _http : HttpClient,
    private _router: Router
  ) { this.headers = this.setHeaders(); }

  setHeaders(): HttpHeaders {
    let header = new HttpHeaders()
    return header.set('Content-Type', 'application/json');
  }

  getArticle() : Observable<any>{
    let url = `${this.url}/api/article/`;
    return this._http.get<Article[]>(url, { headers: this.headers })
    .pipe(
      map(data => data),
      catchError(error => error)
    );
  }

  getArticleByName(id : number) : Observable<any>{
    let url = `${this.url}/api/article/${id}`;
    return this._http.get<Article>(url, { headers: this.headers })
    .pipe(
      map(data => data),
      catchError(error => this.errorHandler(error))
    );
  }

  private errorHandler(error: HttpErrorResponse): Observable<any> {
    if (error.status == 400) {
      this._router.navigate(['/page-not-found']);
    }
    return throwError(error);
  }

}
