import { Component, OnInit, AfterViewInit } from '@angular/core';
import { SendMailService } from './services/send-mail.service';
import { ChapterService } from '../home/services/chapter.service';
import { Chapter } from '../home/interfaces/Chapter';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Http, ResponseContentType } from '@angular/http';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import swal from 'sweetalert2';

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.css']
})
export class NavigationComponent implements OnInit, AfterViewInit {
  chapter: Chapter[] = [];
  // listLangage: string[] = [];
  frmSendEmail: FormGroup;
  // displayE: boolean = false;
  fristChapter: string;
  fristArticle: string;
  constructor(
    private _serviceSendMail: SendMailService,
    private _serviceChapter: ChapterService,
    private _formBuilder: FormBuilder,
    private http: Http
  ) {

  }

  ngAfterViewInit() {
    setTimeout(() => {
      // click navigation

      $(document).ready(function () {

        $('#menu-anatomy-show > a').click(function () {
          $('#menu-anatomy-show').slideToggle('fast');
          $('html, body').removeClass('noscroll');
          // $("#menu-anatomy").toggleClass('fa-minus');
          if ($('#menu-anatomy').hasClass('fa-plus')) {
            $('#menu-anatomy').removeClass('fa-plus');
            $('#menu-anatomy').addClass('fa-minus');
          } else {
            $('#menu-anatomy').removeClass('fa-minus');
            $('#menu-anatomy').addClass('fa-plus');
          }
          $('#bg-anatomy').toggleClass('bg-menu-anatomy');
          $('#menu-startup-show').hide('fast');
          $('#menu-startup-show').hide('fast');
          if ($('#menu-startup').hasClass('fa-times')) {
            $('#menu-startup').removeClass('fa-times');
            $('#menu-startup').addClass('fa-bars');
          }
        });
      });
    }, 1000);
  }

  ngOnInit() {
    
    this._serviceChapter.getChapter().subscribe(
      data => {
        this.chapter = data.results;
        this.fristChapter = data.results[0].slug;
        this.fristArticle = data.results[0].articles[0].slug;
      }
    );
    // this._serviceSendMail.listLanguage().subscribe(
    //   data => {
    //     // console.log(data);
    //     this.listLangage = data;
    //   }
    // );
  }

}
