import { Injectable } from '@angular/core';
import { map, catchError } from 'rxjs/operators';

import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class NewLetterService {
  url : string = environment.apiUrl;
  headers: HttpHeaders;
  constructor(
    private _http : HttpClient
  ) { this.headers = this.setHeaders(); }

  setHeaders(): HttpHeaders {
    let header = new HttpHeaders()
    return header.set('Content-Type', 'application/json');
  }

  sendNewLetter( email : any) : Observable<any>{
    let url = `${this.url}/api/user/sign-up/`;
    return this._http.post(url, email, { headers: this.headers })
    .pipe(
      map(data => data),
      catchError(error => error)
    )
  }

  downloadNow(language : any) : Observable<any>{
    let url = `${this.url}/api/user/download-file/`;
    return this._http.post(url, language, { headers: this.headers });
  }

}
