import { Injectable } from '@angular/core';
import { IStories } from './../interfaces/IStories';
import { Observable } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
@Injectable({
  providedIn: 'root'
})
export class StoriesService {
  url : string = environment.apiUrl;
  headers: HttpHeaders;
  constructor(
    private _http : HttpClient
  ) { this.headers = this.setHeaders(); }

  setHeaders(): HttpHeaders {
    let header = new HttpHeaders()
    return header.set('Content-Type', 'application/json');
  }

  getStories() : Observable<any>{
    let url = `${this.url}/api/stories/`;
    return this._http.get<IStories[]>(url, { headers: this.headers })
    .pipe(
      map(data => data),
      catchError(error => error)
    );
  }

  getDetailStories(id : string) : Observable<any>{
    let url = `${this.url}/api/stories/${id}/`;
    return this._http.get<IStories>(url, { headers: this.headers })
    .pipe(
      map(data => data),
      catchError(error => error)
    );
  }
}
