import { LOCAL_STORAGE , WINDOW} from '@ng-toolkit/universal';
import { Component, OnInit, AfterViewInit , Inject} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ArticleService } from './../../../../../home/services/article.service';
import { ChapterService } from './../../../../../home/services/chapter.service';
import { Article } from './../../../../../home/interfaces/Article';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { NewLetterService } from './../../../../../footer/services/new-letter.service';
import swal from 'sweetalert2';

@Component({
  selector: 'app-chapter-heart-content-grid-article',
  templateUrl: './chapter-heart-content-grid-article.component.html',
  styleUrls: ['./chapter-heart-content-grid-article.component.css']
})
export class ChapterHeartContentGridArticleComponent implements OnInit, AfterViewInit {
  article: Article[] = [];
  color: string;
  chapterName: string;

  frmPopupNews: FormGroup;
  isDisplay: boolean = false;

  key: string;
  articlesSlug: string;
  constructor(@Inject(WINDOW) private window: Window, @Inject(LOCAL_STORAGE) private localStorage: any, 
    private _activatedRoute: ActivatedRoute,
    private _serviceAriticle: ArticleService,
    private _serviceChapter: ChapterService,
    private _serviceNewsLetter: NewLetterService,
    private _formBuilder: FormBuilder,
    private _router: Router
  ) {

  }

  ngOnInit() {
    this.createForm();
    this._activatedRoute.params.subscribe(
      data => this.getChapter(data.name)
    );
  }

  ngAfterViewInit() {
    this.window.scrollTo(0, 0);
  }


  getAllArticle(id: number) {
    this.article = [];
    this._serviceAriticle.getArticle().subscribe(
      data => {
        data.results.forEach(element => {
          if (element.chapter_id == id) {
            this.article.push(element);
          }
        });
      }
    )
  }

  getChapter(name: string) {
    this._serviceChapter.getChapterByName(name).subscribe(
      data => {
        this.getAllArticle(data.id);
        this.color = data.color;
      }
    )
  }

  popUpNewsLetter(articlesSlug) {
    this.articlesSlug = articlesSlug;
    if (this.localStorage.getItem(this.key) != null) {
      // We have items key = email
      this._activatedRoute.params.subscribe(
        data => this._router.navigate([data.name, articlesSlug])
      );

    } else {
      // No items
      var modal = document.getElementById('popup-newsletter');
      var button = document.getElementById('close-btn-download-news');
      modal.style.display = "block";
      button.onclick = function () {
        modal.style.display = "none";
      }
      // When the user clicks anywhere outside of the modal, close it
      this.window.onclick = function (event) {
        if (event.target == modal) {
          modal.style.display = "none";
        }
      }
    }

  }

  createForm() {
    this.frmPopupNews = this._formBuilder.group({
      name: ['', [
        Validators.required,
        Validators.minLength(1),
        Validators.maxLength(50)
      ]],
      email: ['', [
        Validators.required,
        Validators.maxLength(50),
        Validators.pattern('[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}')
      ]]
    });
  }


  onSubmitFormNews() {
    if (this.frmPopupNews.valid) {
      //console.log(this.frmPopupNews.value);
      this.localStorage.setItem(this.key, this.frmPopupNews.value.email);
      this._serviceNewsLetter.sendNewLetter(this.frmPopupNews.value).subscribe(data => data);
      this.frmPopupNews.reset();
      swal({
        title: 'Subscribed',
        type: 'success',
        confirmButtonText: 'OK'
      }).then((results) => {
        if (results.value) {
          this._activatedRoute.params.subscribe(
            data => {
              this._router.navigate([data.name, this.articlesSlug], { fragment: 'content-in-chapter' } );
            }
          );
        }
      })
      this.isDisplay = !this.isDisplay;
    } else {
      this.validateAllFormFields(this.frmPopupNews);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {         //{1}
    Object.keys(formGroup.controls).forEach(field => {    //{2}
      const control = formGroup.get(field);               //{3}
      if (control instanceof FormControl) {             //{4}
        control.markAsDirty({ onlySelf: true });
      } else if (control instanceof FormGroup) {        //{5}
        this.validateAllFormFields(control);            //{6}
      }
    });
  }

}
