import { WINDOW } from '@ng-toolkit/universal';
import { Component, OnInit, AfterViewInit, AfterViewChecked , Inject} from '@angular/core';
import { IStories } from './../interfaces/IStories';
import { StoriesService } from './../services/stories.service';
// import { Meta } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-stories',
  templateUrl: './stories.component.html',
  styleUrls: ['./stories.component.css']
})
export class StoriesComponent implements OnInit, AfterViewInit, AfterViewChecked {

  stories: IStories[] = [];
  storiesload: IStories[] = [];
  limit: number = 8;
  count: number;
  check: boolean = false;
  private scrollExecuted: boolean = false;
  constructor(@Inject(WINDOW) private window: Window, 
    private _serviceStories: StoriesService,
    private activatedRoute: ActivatedRoute
  ) {
    this.count = 0;
    // const url = window.location.href;
    // this.meta.updateTag({ property: 'og:title', content: 'Stories' });
    // this.meta.updateTag({ property: 'og:url', content: `${url}` });
    // this.meta.updateTag({ property: 'og:type', content: 'website' });
    // this.meta.updateTag({ property: 'og:description', content: 'Along the way we have heard the inspiring stories of migrant and refugees entrepreneurs from different backgrounds and countries...' });
    // this.meta.updateTag({ property: 'og:image', content: 'https://www.hatchquarter.com.au/assets/images/OG.jpg' });
  }

  ngOnInit() {
    this.getAllStories();

  }
  ngAfterViewInit() {
    this.window.scrollTo(0, 0);
  }

  ngAfterViewChecked() {
    if (!this.scrollExecuted) {
      let routeFragmentSubscription: Subscription;

      // Automatic scroll
      routeFragmentSubscription = this.activatedRoute.fragment.subscribe(
        fragment => {
          if (fragment) {
            let element = document.getElementById(fragment);
            //console.log(element);
            if (element) {
              //element.scrollIntoView();
              $("html, body").animate({ scrollTop: $([element]).offset().top-100 }, 1000);
              this.scrollExecuted = true;

              // Free resources
              setTimeout(
                () => {
                  //console.log('routeFragmentSubscription unsubscribe');
                  routeFragmentSubscription.unsubscribe();
                }, 1000);

            }
          }
        });
    }

  }


  getAllStories() {
    this._serviceStories.getStories().subscribe(
      data => {
        this.storiesload = data.results;
        this.storiesload.forEach((element, index) => {
          if (index < this.limit) {
            this.stories.push(element)
            this.count++;
          }
        })
      }
    )
  }

  btnLoadMore() {
    for (let i = this.count; i < this.storiesload.length; i++) {
      //if (i == (this.count + 4)) break;
      this.stories.push(this.storiesload[i]);
      this.count ++;
    }
    //this.count += 4;
    if (this.count >= this.storiesload.length) this.check = true;
  }

}
