import { WINDOW } from '@ng-toolkit/universal';
import { Component, OnInit , Inject} from '@angular/core';
import { ChapterService } from './../../../modules/home/services/chapter.service';
import { Chapter } from './../../../modules/home/interfaces/Chapter';

@Component({
  selector: 'app-explore-chapter',
  templateUrl: './explore-chapter.component.html',
  styleUrls: ['./explore-chapter.component.css']
})
export class ExploreChapterComponent implements OnInit {
  chapter : Chapter;
  constructor(@Inject(WINDOW) private window: Window, 
    private _serviceChapter : ChapterService
  ) { }

  ngOnInit() {
    this.getAllChapter();
  }

  getAllChapter(){
    this._serviceChapter.getChapter().subscribe(
      data =>  this.chapter = data.results
    )
  }

  scrollArticle(){
    this.window.scrollTo(0, 0);
  }
}
