import { WINDOW } from '@ng-toolkit/universal';
import { Component, OnInit, AfterViewInit , Inject} from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { IContact } from './../interfaces/IContact';
import { ContactService } from './../services/contact.service';
import swal from 'sweetalert2';
import { Meta } from '@angular/platform-browser';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit, AfterViewInit {

  frmUser : FormGroup;
  contact : IContact;

  constructor(@Inject(WINDOW) private window: Window, 
    private _formBuilder : FormBuilder,
    private _serviceContact : ContactService,
    private meta: Meta
  ) {
    // const url = window.location.href;
    this.meta.updateTag({ property: 'og:title', content: 'Contact Us' });
    // this.meta.updateTag({ property: 'og:url', content: `${url}` });
    this.meta.updateTag({ property: 'og:type', content: 'website' });
    // this.meta.updateTag({ property: 'og:description', content: 'Let us know your thoughts...' });
    // this.meta.updateTag({ property: 'og:image', content: 'https://www.hatchquarter.com.au/assets/images/OG.jpg' });
   }

  ngOnInit() {
    this.createForm();
  }

  ngAfterViewInit() {
    this.window.scrollTo(0, 0);
 }

  createForm(){
    this.frmUser = this._formBuilder.group({
      name : ['',[
        Validators.required,
        Validators.minLength(5),
        Validators.maxLength(50),
        Validators.pattern('[a-zA-Z0-9 ]{1,}')
      ]],
      email : ['',[
        Validators.required,
        Validators.maxLength(50),
        Validators.pattern('[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}')
      ]],
      phone_number : ['',[
        Validators.minLength(10),
        Validators.maxLength(10),
        Validators.pattern('[0-9]{1,}')
      ]],
      comment : ['',[
        Validators.required,
        Validators.pattern('[a-zA-z0-9 ]{1,}')
      ]],
    });
  }

  onSubmitForm(){
    if(this.frmUser.valid){
      this.contact = Object.assign({}, this.frmUser.value);
      //console.log(this.contact);
      this._serviceContact.addContact(this.contact).subscribe(data => data);
      // console.log(this.contact)
      swal({
        title : 'Success',
        text : 'Contact Send !',
        type : 'success',
        confirmButtonText: 'OK'
      })
      this.frmUser.reset();
    }else{
      this.validateAllFormFields(this.frmUser);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {         //{1}
  Object.keys(formGroup.controls).forEach(field => {    //{2}
    const control = formGroup.get(field);               //{3}
      if (control instanceof FormControl) {             //{4}
        control.markAsDirty({ onlySelf: true });
      } else if (control instanceof FormGroup) {        //{5}
        this.validateAllFormFields(control);            //{6}
      }
    });
  }

}
