import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { HttpClient, HttpErrorResponse , HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { Chapter } from './../interfaces/Chapter';
import { Router } from '../../../../../node_modules/@angular/router';

@Injectable({
  providedIn: 'root'
})
export class ChapterService {
  
  url : string = environment.apiUrl;
  headers: HttpHeaders;
  constructor(
    private _http : HttpClient,
    private _router: Router
  ) { this.headers = this.setHeaders(); }

  setHeaders(): HttpHeaders {
    let header = new HttpHeaders()
    return header.set('Content-Type', 'application/json');
  }

  getChapter() : Observable<any>{
    let url = `${this.url}/api/chapter/`;
    return this._http.get<Chapter[]>(url, { headers: this.headers })
    .pipe(
      map(data => data),
      catchError(error => this.errorHandler(error))
    );
  }

  getChapterByName(name : string) : Observable<any>{
    let url = `${this.url}/api/chapter/${name}`;
    return this._http.get<Chapter>(url, { headers: this.headers })
    .pipe(
      map(data => data),
      catchError(error => this.errorHandler(error))
    );
  }

  private errorHandler(error: HttpErrorResponse): Observable<any> {
    if (error.status == 400||error.status == 404) {
      this._router.navigate(['/page-not-found']);
    }
    return throwError(error);
  }

}
