import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { HttpClient, HttpHeaders  } from '@angular/common/http';
import { environment } from '../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class SendMailService {
  url : string = environment.apiUrl;
  headers: HttpHeaders;
  constructor(
    private _http : HttpClient
  ) { this.headers = this.setHeaders(); }

  setHeaders(): HttpHeaders {
    let header = new HttpHeaders()
    return header.set('Content-Type', 'application/json');
  }

  sendMail(email : any) : Observable<any>{
    let url = `${this.url}/api/user/send-file/`;
    return this._http.post(url, email , { headers: this.headers });
  }

  listLanguage() :Observable<any>{
    let url = `${this.url}/api/file/listlanguage/`;
    return this._http.get<string[]>(url , { headers: this.headers })
    .pipe(
      map(data => data)
    );
  }
}
